@import "../../inc/vars";
.acro-compare-features {
	.acro-compare-features__container {
		padding: 48px 20px;
		@media(min-width: $screen-md) {
			padding: 60px 80px;
		}
		.acro-compare-features_title {
			text-align: center;
			h2 {
				@include pop(400, 35, 1.24);
				background: var(--grad-title, linear-gradient(180deg, #2D3442 0%, #6C7689 100%));
				background-clip: text;
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
				margin-bottom: 8px;
				@media(min-width: $screen-sm) {
					margin-bottom: 16px;
					@include pop(400, 44, 1.24);
				}
			}
			.subtitle {
				@include fig(400, 20, 1.56);
				color: #4F4B58;
				margin-bottom: 40px;
			}
		}
		.acrobits-features-container {
			display: flex;
			flex-wrap: wrap;
			.features {
				width: 100%;
				display: flex;
				flex-wrap: wrap;
				background: linear-gradient(180deg, #F2F4F8 0%, #E7E3ED 100%, #F4F4F6 100%);
				padding: 32px 32px 0;
				justify-content: space-between;
				overflow: hidden;
				margin-bottom: 15px;
				flex-direction: column;
				@media(min-width: $screen-sm) {
					width: calc(50% - 16px);
					margin-right: 8px;
					margin-left: 8px;
				}
				@media(min-width: $screen-md) {
					flex-direction: row;
				}
				@media(min-width: $screen-mlg) {
					padding: 49px 79px;
				}
				.features-content {
					width: 100%;
					margin-bottom: 43px;
					@media(min-width: $screen-md) {
						width: 50%;
					}
					.company-logo {
						margin-bottom: 24px;
					}
					h3 {
						@include pop(400, 29, 1.24);
						margin-bottom: 12px;
						background: var(--grad-title, linear-gradient(180deg, #2D3442 0%, #6C7689 100%));
						background-clip: text;
						-webkit-background-clip: text;
						-webkit-text-fill-color: transparent;
					}
					ul {
						list-style-type: none;
						margin-left: 0;
						padding-left: 0;
						li {
							@include fig(400, 18, 20px);
							color: #4F4B58;
							margin-bottom: 14px;
							position: relative;
							padding-left: 40px;
							&:before {
								content: '';
								display: block;
								width: 24px;
								height: 24px;
								background: url(/wp-content/themes/acro/images/counterpath-page/check-green.svg) no-repeat;
								margin-right: 16px;
								position: absolute;
								left: 0;
								top: 0;
							}
							&:last-child {
								margin-bottom: 0;
							}
							.stars-disabled {
								color: #D0CDD5;
							}
						}
					}
				}
				&.acrobits-features {
					ul {
						li {
							color: #1E8F69;
						}
					}
				}
				&.counterpath-features {
					ul {
						li {
							color: #8C8698;
							&:before {
								background: url(/wp-content/themes/acro/images/counterpath-page/check-grey.svg) no-repeat;
							}
						}
					}
				}
				.features-images {
					width: 100%;
					text-align: center;
					@media(min-width: $screen-md) {
						width: 50%;
					}
					.image {
						display: inline-block;
						position: relative;
						max-height: 300px;
						overflow: hidden;
						@media(min-width: $screen-md) {
							max-height: none;
						}
						.main-image {
							max-width: 100%;
						}
						.second-screen {
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							animation: slide-up 10s linear infinite;
						}
					}
				}
			}
		}
	}
}

@keyframes slide-up {
	0% {
		transform: translateY(-20%);
		opacity: 0;
	}
	30% {
		transform: translateY(-20%);
		opacity: 0;
	}
	40% {
		transform: translateY(0);
		opacity: 1;
	}
	80% {
		transform: translateY(0);
		opacity: 1;
	}
	90% {
		transform: translateY(-20%);
		opacity: 0;
	}
	100% {
		transform: translateY(-20%);
		opacity: 0;
	}
}